import React from "react";
import PropTypes from "prop-types";
import "./SeatMap.scss";
import { newSeatMap } from "../../../tools/mockData";
import { Link } from "react-router-dom";
const SeatMapPage = ({
  passenger,
  passengers,
  select,
  onSave,
  backUrl,
  inFlight = false,
  isAdmin = false,
  saving = false,
}) => {
  const statusList = inFlight
    ? passengers.map((passenger1) => {
        return passenger1.seat && passenger1.specialMeals.length > 0
          ? { seat: passenger1.seat, status: "special-meal" }
          : passenger1.seat
          ? { seat: passenger1.seat, status: "reserved" }
          : {};
      })
    : passengers.map((passenger1) => {
        const status =
          passenger1.wheelChair && passenger1.infants > 0
            ? "both"
            : passenger1.infants > 0
            ? "infants"
            : passenger1.wheelChair
            ? "wheelChair"
            : "reserved";
        return passenger1.seat ? { seat: passenger1.seat, status } : {};
      });

  return (
    <>
      <div className="row">
        <div className="col-8">
          {newSeatMap.seatmap.map((seat) => {
            return (
              <div key={seat.id}>
                <div
                  id={seat.id}
                  name={seat.number}
                  className={
                    passenger && passenger.seat === seat.number
                      ? "selected"
                      : passengers.length > 0
                      ? (
                          statusList.find((a) => a.seat === seat.number) || {
                            status: "available",
                          }
                        ).status
                      : seat.status
                  }
                  onClick={
                    passenger &&
                    (() =>
                      select({
                        id: seat.id,
                        number: seat.number,
                      }))
                  }
                >
                  <h4 id="seat-number">{seat.number}</h4>
                </div>
                {parseInt(seat.id, 10) % 2 === 0 && (
                  <div className="clearBoth"></div>
                )}
              </div>
            );
          })}
          <div className="row">
            {passenger && !isAdmin && (
              <div className="col">
                <button
                  style={{ marginTop: 20 }}
                  className="btn btn-primary"
                  onClick={passenger && onSave}
                >
                  {saving ? "saving.." : "save"}
                </button>
              </div>
            )}
            <div className="col">
              <Link
                style={{ marginTop: 20 }}
                className="btn btn-primary"
                to={backUrl}
              >
                Back
              </Link>
            </div>
          </div>
        </div>
        <div className="col-4">
          <div className="colorCode" style={{ background: "#d8d8d8" }}></div>
          Available
          <div className="colorCode" style={{ background: "#ffa500" }}></div>
          Reserved
          {inFlight ? (
            <>
              <div
                className="colorCode"
                style={{ background: "#ac0788" }}
              ></div>
              Special Meal
            </>
          ) : (
            <>
              <div
                className="colorCode"
                style={{ background: "#3366cc" }}
              ></div>
              selected
              <div
                className="colorCode"
                style={{ background: "#c52b44" }}
              ></div>
              WheelChair
              <div
                className="colorCode"
                style={{ background: "#ac0788" }}
              ></div>
              Infants
              <div
                className="colorCode"
                style={{ background: "#04640c" }}
              ></div>
              WheelChair + Infants
            </>
          )}
        </div>
      </div>
      {passenger && (
        <p className="information">**Click on the same seat to de-select</p>
      )}
    </>
  );
};

SeatMapPage.propTypes = {
  passenger: PropTypes.object,
  passengers: PropTypes.array.isRequired,
  select: PropTypes.func,
  onSave: PropTypes.func,
  backUrl: PropTypes.string.isRequired,
  inFlight: PropTypes.bool,
  isAdmin: PropTypes.bool,
  saving: PropTypes.bool,
};
export default SeatMapPage;
