import React, { useEffect, useState } from "react";
import FlightDetails from "./FlightDetails";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { loadFlights } from "../../redux/actions/flightActions";
import { loadPassengers } from "../../redux/actions/passengerActions";
import { newFlight } from "../../../tools/mockData";
import SeatMapPage from "../seatmap/SeatMapPage";
import Spinner from "../common/Spinner";

function FlightDetailsPage({
  loadFlights,
  flights,
  passengers,
  loadPassengers,
  flight,
  isAdmin,
  ...props
}) {
  //add seatmap call and seatmap state
  const [flightPassengers, setFlightPassengers] = useState([
    ...props.flightPassengers,
  ]);
  useEffect(() => {
    if (flights.length === 0) {
      loadFlights().catch((error) => alert("Loading flights failed " + error));
    }
    if (passengers.length === 0) {
      loadPassengers().catch((error) =>
        alert("Loading passengers failed " + error)
      );
    } else if (flights.length > 0) {
      setFlightPassengers([...props.flightPassengers]);
    }
  }, [props.flightPassengers]);

  return (
    <>
      <h2>Flight Detail</h2>
      {props.flightPassengers.length === 0 ? (
        <Spinner />
      ) : (
        <>
          <div className="row">
            <div className="col-md-8">
              <FlightDetails
                flight={flight}
                isAdmin={isAdmin}
                passengers={passengers}
              ></FlightDetails>
            </div>
            <div className="col-md-4">
              <SeatMapPage
                backUrl={isAdmin ? "/admin/flights" : "/staff/flights"}
                passengers={flightPassengers}
              ></SeatMapPage>
            </div>
          </div>
        </>
      )}
    </>
  );
}

FlightDetailsPage.propTypes = {
  flights: PropTypes.array.isRequired,
  passengers: PropTypes.array.isRequired,
  loadFlights: PropTypes.func.isRequired,
  loadPassengers: PropTypes.func.isRequired,
  flight: PropTypes.object.isRequired,
  isAdmin: PropTypes.bool.isRequired,
  flightPassengers: PropTypes.array.isRequired,
};

function getFlightByName(flights, name) {
  return flights.find((a) => a.name === name) || newFlight;
}

function mapStateToProps(state, ownProps) {
  const name = ownProps.match.params.name;
  const path = ownProps.match.path;
  const isAdmin = path === "/admin/flight/:name";
  const flight =
    name && state.flights.length > 0
      ? getFlightByName(state.flights, name)
      : newFlight;
  const flightPassengers =
    flight.id && state.passengers.length > 0
      ? state.passengers.filter((passenger) => passenger.flightId === flight.id)
      : [];
  return {
    flight,
    isAdmin,
    flightPassengers,
    flights: state.flights,
    passengers: state.passengers,
    seatmaps: state.seatmaps,
  };
}

const mapDispatchToProps = {
  loadFlights,
  loadPassengers,
};
export default connect(mapStateToProps, mapDispatchToProps)(FlightDetailsPage);
